.search-container {
    width: 30%;
    height: 100%;
    background-color: navy;
    display: flex;
    flex-direction: column;
  }
  
  .search-bar {
    height: 10%;
    background-color: #3e3b51;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
  }
  
  #site-search {
    height: 60%;
    width: 70%;
    padding-left: 3%;
  }
  
  .search-icon {
    cursor: pointer;
  }
  
  .city-list {
    background: #3e3b51;
    overflow: auto;
    border-collapse: inherit;
    position: fixed;
    top: 10vh;
    bottom: 0;
    left: 0;
    padding: 0;
    width: 30%;
    margin: 0;
    list-style: none;
    display: flex;
    flex-direction: column;
  }
  
  .city-item {
    cursor: pointer;
    padding: 5% 0 5% 3%;
    width: 80%;
    color: white;
    display: flex;
    margin: 5% 7%;
  }
  
  .city-item:hover {
    border: 1px solid white;
  }
  
  div > .city-list {
    z-index: 100;
  }