.forecast-container {
    width: 70%;
    height: 100%;
    background-color: skyblue;
    display: flex;
    flex-direction: column;
  }
  
  .unit-toggle {
    height: 20%;
    background-color: #24142c;
    color: white;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-right: 3%;
    align-items: flex-end;
  }
  
  .forecast-list {
    height: 80%;
    background-color: #24142c;
    color: white;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding-left: 3%;
    padding-right: 3%;
  }

  .forecast-card {
    width: 15%;
    height: 75%;
    background-color: #3e3b51;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
  }
  
  .forecast-icon {
    height: 100px;
    width: 100px;
  }
  
  .forecast-temp {
    font-size: 13px;
  }