.highlights-container {
    height: 60%;
    display: flex;
    background-color: #24142c;
    flex-direction: column;
  }
  
  .highlights-header {
    height: 15%;
    background-color: #24142c;
    text-align: center;
    color: white;
    margin-left: 7%;
    margin-right: 7%;
  }
  
  .highlights-body {
    height: 70%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    padding-left: 2%;
    padding-right: 2%;
  }
  
  .highlights-footer {
    height: 15%;
    background-color: #24142c;
    color: white;
  }

  .highlight-card {
    width: 40%;
    height: 40%;
    background-color: #3e3b51;
    color: white;
  }
  
  .highlight-card > div {
    width: 100%;
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: space-evenly;
    align-items: center;
  }
  
  .humidity-bar {
    width: 100%;
    display: flex;
    justify-content: center;
  }
  
  .humidity-bar > div {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 80%;
    justify-content: space-evenly;
  }
  
  .highlight-title,
  .highligh-value {
    margin: unset;
  }
  
  .wind-direction {
    display: flex;
    width: 30%;
    align-items: center;
    justify-content: space-evenly;
  }
  
  .wind-direction > svg {
    transform-origin: 0.4375em 0.5em;
    background: white;
    color: #24142c;
    border: #24142c;
    padding: 3%;
    border-radius: 50%;
  }
  
  .unit-change-buttons {
    border-radius: 50%;
    padding: 5%;
    margin: 2%;
    border: none;
    cursor: pointer;
    font-weight: bold;
  }
  
  .unit-change-buttons:hover {
    background-color: grey;
  }